import {Howl} from 'howler';

var footstepWood = require('url:./sounds/footstep-wood.ogg');
var footstepTile = require('url:./sounds/footstep-tile.ogg');
var footstepWater = require('url:./sounds/footstep-water.ogg');
var footstepGravel = require('url:./sounds/footstep-gravel.ogg');
var footstepGrass = require('url:./sounds/footstep-grass.ogg');

export type Howls = { [id:string]: Howl };

export function setupSounds(sounds:Howls) {
    sounds.footstepWood = new Howl({src: [footstepWood]})
    sounds.footstepTile = new Howl({src: [footstepTile]})
    sounds.footstepWater = new Howl({src: [footstepWater]})
    sounds.footstepGravel = new Howl({src: [footstepGravel]})
    sounds.footstepGrass = new Howl({src: [footstepGrass]})
}
